@font-face {
  font-family: 'icomoon';
  src:  url('fonts/icomoon.eot?rkv08f');
  src:  url('fonts/icomoon.eot?rkv08f#iefix') format('embedded-opentype'),
    url('fonts/icomoon.ttf?rkv08f') format('truetype'),
    url('fonts/icomoon.woff?rkv08f') format('woff'),
    url('fonts/icomoon.svg?rkv08f#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-star-empty:before {
  content: "\e9d7";
}
.icon-star-full:before {
  content: "\e9d9";
}
.icon-cross:before {
  content: "\ea0f";
}
